* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: #808080;
  font-family: "myriad-pro", Helvetica, Arial, sans-serif;
  background-color: #ebebeb;
}
.Nav_top {
  width: 100%;
  background-color: #fff;
  padding: 0 50px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.C_logo {
  padding-top: 40px;
}
.Need_hlp {
  display: flex;
  justify-content: flex-end;
}
.Need_hlp a {
  text-decoration: none;
  color: #008bdf;
}
.Need_hlp p {
  width: 280px;
  background-color: rgba(128, 128, 128, 0.527);
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0 0 10px 10px;
}
.Menu,
.Skills_mid {
  display: none;
}
.Company_list {
  width: 100%;
  height: 100px;
  background-color: #60b9ee;
  display: flex;
  padding: 0 4rem;
  align-items: center;
  justify-content: space-between;
}
.Company_location {
  color: #fff;
}
.Company_list .list ol {
  display: flex;
}
.Company_list .list ol li {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
  color: #fff;
}
.list .fa-long-arrow-alt-right {
  padding: 4px 4px 0 4px;
  font-size: 25px;
  cursor: default;
}
.Skills_Box {
  border: 1px solid #ebebeb;
  margin: 25px 35px;
  border-radius: 5px;
  padding: 10px 0;
  background-color: #fff;
  height: auto;
  box-shadow: 1px 1px 5px #808080;
}
.S_EQ {
  text-align: center;
  font-size: 27px;
  color: #0093dd;
  padding: 10px;
  border-bottom: 1px solid #80808081;
}
.rating_p {
  margin: 25px;
  font-size: 22px;
}
.btn_laststep {
  display: none;
}
.skills_list_box {
  margin-top: 3rem;
}
.Skills_Sec,
.Skills_Sec_1 {
  background-color: #d1e5f19d;
  display: flex;
  padding: 10px;
  margin-bottom: 5px;
  font-size: 22px;
  align-items: center;
  /* justify-content: space-around; */
}
.Heading_skill {
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.EXPRNCE,
.EXPRNCE_1 {
  display: flex;
  width: 65%;
  align-items: center;
  justify-content: center;
}
.EXPRNCE > div,
.EXPRNCE_1 > div {
  border: none;
  border-radius: 10px;
  margin: 5px 20px;
  cursor: pointer;
  background-color: #b3dbf19d;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 150px;
  height: 70px;
  padding: 15px;
}
.EXPRNCE > div:hover,
.EXPRNCE_1 > div:hover {
  background-color: #60b9ee;
  color: #fff;
}
.Heading_skill h3 {
  width: 300px;
}
.EXPRNCE_1 > div {
  text-align: end;
}
.Skills_Sec_1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Skills_buttons {
  margin: 20px 0;
  display: flex;
  padding: 2rem 6rem 0 6rem;
  justify-content: space-between;
  align-items: center;
}

.Prvs_btn button {
  width: 170px;
}
.Nxt_btn button {
  width: 140px;
  margin-right: 20px;
}
.Prvs_btn button,
.Nxt_btn button {
  padding: 12px;
  display: flex;
  font-size: 18px;
  border-radius: 25px;
  font-weight: bold;
  border: none;
  background-color: #80c844;
  color: #fff;
  align-items: center;
  justify-content: space-between;
}

